import { Col, Collapse, Form, Input, Row } from 'antd';
import { CaretRightOutlined } from '@ant-design/icons';
import './styles/app.css';
import telegram from './assets/telegram.png';
import facebook from './assets/facebook.png';
import info from './assets/info.svg';
import { useEffect, useState } from 'react';
import ModalHowto from './components/ModalHowto';
// import onSubmit from './utils/submit';

const { Panel } = Collapse;

const App = () => {
  const [visible, setVisible] = useState<boolean>(false);
  const [email, setEmail] = useState<string>('');
  const [wallet, setWallet] = useState<string>('');

  const onShareClick = () => {
    window.FB.ui({
      method: 'share',
      href: 'https://developers.facebook.com/docs/',
    }, function(res: any) {
      if (res && !res.error_message) {
        alert('Posting completed.');
      } else {
        alert('Error while posting.');
      }
    });
  };

  const handleSubmit = (e: any) => {
    e.preventDefault();
    // onSubmit({email, wallet});
  }

  useEffect(() => {
    window.fbAsyncInit = () => {
      window.FB.init({
        appId            : '1154272804982160',
        autoLogAppEvents : true,
        xfbml            : true,
        version          : 'v11.0'
      });
    };
    (function (d, s, id) {
      var js: any, fjs: any = d.getElementsByTagName(s)[0];
      if (d.getElementById(id)) { return; }
      js = d.createElement(s); js.id = id;
      js.src = "https://connect.facebook.net/en_US/sdk.js";
      fjs.parentNode.insertBefore(js, fjs);
    }(document, 'script', 'facebook-jssdk'));
  }, []);

  return (
    <div className='home__container'>
      <div className='spacing' />
      <h1>KHNEAR Airdrop</h1>
      <p>The first Probinexs airdrop is here and we are super generous. Fill in all the tasks and increase your chances to win 2000 PBX! </p>
      <div className='home__bgStep'>
        <h3>3 Step To Get Airdrop</h3>
      </div>
      <Collapse
        bordered={false}
        defaultActiveKey={['1']}
        expandIcon={({ isActive }) => <CaretRightOutlined rotate={isActive ? 90 : 0} />}
        className="site-collapse-custom-collapse"
      >
        <Panel header="Join @KHNEAR Community on Telegram" key="1" className="site-collapse-custom-panel">
          <Row justify='center'>
            <button className='button-50'>
              <a style={{color: 'inherit'}} href='https://t.me/khnear' target='_blank' rel="noreferrer">
                <Row gutter={[8,8]} align='middle'>
                  <Col>
                    <img src={telegram} alt='' height={20} />
                  </Col>
                  <Col>
                    <span>Join Community</span>
                  </Col>
                </Row>
              </a>
            </button>
          </Row>
        </Panel>
        <Panel header="Share post on Facebook" key="2" className="site-collapse-custom-panel">
          <Row justify='center'>
            <button className='button-50' onClick={onShareClick}>
              <Row gutter={[8,8]} align='middle'>
                <Col>
                  <img src={facebook} alt='' height={20} />
                </Col>
                <Col>
                  <span>Share to Facebook</span>
                </Col>
              </Row>
            </button>
          </Row>
        </Panel>
        <Panel 
          header={
            <Row justify='space-between'>
              <Col><span>Submit Your Wallet Address</span></Col>
              <Col><img onClick={()=> setVisible(!visible)} src={info} alt='' width={20} /></Col>
            </Row>
          } 
          key="3" 
          className="site-collapse-custom-panel"
        >
          <div>
            <Form layout='vertical'>
              <Form.Item label='Email Address:'>
                <Input value={email} onChange={(e) => setEmail(e.target.value)} size='large' placeholder='Enter email address' />
              </Form.Item>
              <Form.Item label='Wallet Address:'>
                <Input value={wallet} onChange={(e) => setWallet(e.target.value)} size='large' placeholder='Enter wallet address' />
              </Form.Item>
              <Form.Item>
                <button onClick={handleSubmit} style={{width: '100%'}} className='button-50'>
                  Submit
                </button>
              </Form.Item>
            </Form>
          </div>
        </Panel>
      </Collapse>

      <ModalHowto />
    </div>
  )
}

export default App;
